import {dict} from "../config";

export default function Header(props){
  let languageTitle = '';
  let date = props.date;
  date = date.split('-').reverse().join('.');
  props.language === 'ru' ? languageTitle = 'en' : languageTitle = 'ru';
  return(
    <header>
      <h1>exchange rates</h1>
      <div>
        <span>
          <span>{dict.titles.update[props.language]}</span>
          <span className='date'>{date}</span>
        </span>
        <span className='language' onClick={props.changeLanguage}>{languageTitle}</span>
      </div>
    </header>
  )
}