import {useContext} from 'react';
import Context from './Context';
import {dict} from '../config';

export default function CurrenciesBlock(){
  const {addElem, currenciesBlock, language: language} = useContext(Context);

  let currencies = currenciesBlock.map((item, index) =>
    <div key={item} onClick={() => addElem(index, item)} className="tooltip"
         tabIndex='0' onKeyDown={(e) => {if (e.key === 'Enter') addElem(index, item)}}
    >
      <span className="tooltiptext">{dict.currencies[item][language]}</span>
      {item}
    </div>
  )

  return(
    <div className='currencies-block'>{currencies}</div>
  )
}