import {useContext, useState} from 'react';
import Context from './Context';
import {dict} from '../config';
import {logDOM} from "@testing-library/react";

export default function InputItem(props){
  const {values, calculateRate, language: language} = useContext(Context);
  let value = +values.get(props.title);
  value = value === 0 ? '' : +value.toFixed(2);
  value = value > 100000 ? Math.round(value) : value;

  let validate = e => {
    e.target.value = e.target.value.replace(/\D^/g, '');
    if(Number.isFinite(+e.target.value))
      calculateRate(e.target.value, props.title);
  }

  return (
    <div className='input-item'>
      <div>
        <span className='delete' onClick={() => props.deleteElem(props.index, props.title)}
              tabIndex='0' onKeyDown={(e) => {if (e.key === 'Enter') props.deleteElem(props.index, props.title)}}
        >[x]</span>
      </div>
      <form autoComplete='off' name={props.title} onMouseDown={() => document.getElementById(props.title).focus()}>
        <label>{props.title}<br/><span>{dict.currencies[props.title][language]} </span></label>
        <input type='text' id={props.title} name={props.title} value={value} onInput={validate} inputMode="numeric"/>
      </form>
    </div>
  )
};
