import Header from "./components/Header";
import Main from "./components/Main";
import Footer from "./components/Footer";
import {useState} from "react";

export default function App(){
  const[date, setDate] = useState('')
  let memoLanguage = '';
  if (localStorage.getItem('language')) {
    memoLanguage = localStorage.getItem('language')
  } else memoLanguage = 'en';

  const [language, setLanguage] = useState(memoLanguage);

  let changeLanguage = () => {
    language === 'ru'
      ? setLanguage('en') : setLanguage('ru');
  }

  let getDate = (value) => {
    setDate(value);
  }

  localStorage.setItem('language', language);
  return (
    <>
      <div className='content'>
        <Header language={language} changeLanguage={changeLanguage} date={date}></Header>
        <Main language={language} getDate={getDate}></Main>
      </div>
      <Footer language={language}></Footer>
    </>
  )
};
