import {useState} from 'react';
import {useEffect} from 'react';
import Context from './Context';
import InputItem from './InputItem';
import CurrenciesBlock from './CurrenciesBlock';
import {dict} from '../config';

export default function Main(props) {
  let defaultInputBlock = [];
  let defaultValuesRub = 1;
  if (JSON.parse(localStorage.getItem('inputsCurrencies'))
    && JSON.parse(localStorage.getItem('inputsCurrencies')).length > 1) {
    defaultInputBlock = JSON.parse(localStorage.getItem('inputsCurrencies'))
  } else defaultInputBlock = ['USD', 'EUR', 'GBP', 'RUB'];
  if (+localStorage.getItem('RUB') > 0) {
    defaultValuesRub = +localStorage.getItem('RUB')
  } else defaultValuesRub = 10000;

  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [rates, setRates] = useState({});
  const [values, setValues] = useState({});
  const [inputBlock, setInputBlock] = useState(defaultInputBlock);
  const [currenciesBlock, setCurrenciesBlock] = useState([]);

  useEffect(() => {
    getData();
  }, []);
  const getData = async () => {
    await fetch(
      "https://www.cbr-xml-daily.ru/latest.js"
    )
      .then((r) => r.json())
      .then(r => {
          setIsLoaded(true);
          r.rates.RUB = 1;
          setRates(r);
          let arr = [];
          for (let key in r.rates){
            if(key in dict.currencies) {
              if(key === 'XDR') continue
              r.rates[key] = r.rates[key] * defaultValuesRub;
              if (!defaultInputBlock.includes(key))
                arr.push(key);
            }
          }
          arr.sort();
          props.getDate(r.date);
          setCurrenciesBlock(arr);
          setValues(new Map(Object.entries(r.rates)))
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        })
  };

  function calculateRate(inputValue, currency){
    if(isNaN(inputValue) || inputValue === '') inputValue = 0;
    let map = new Map()
    for(let key of values.keys()) {
      let value = +inputValue * +rates.rates[key] / +rates.rates[currency];
      map.set(key, value)
    }
    setValues(map);
    localStorage.setItem('RUB', map.get('RUB'));
  }

  function addElem(index, elem){
    setCurrenciesBlock(
      [...currenciesBlock.slice(0, index), ...currenciesBlock.slice(index + 1)]
    );
    setInputBlock([...inputBlock, elem]);
  }

  function deleteElem(index, elem){
    setInputBlock(
      [...inputBlock.slice(0, index), ...inputBlock.slice(index + 1)]
    );
    setCurrenciesBlock([...currenciesBlock, elem]);
  }

  localStorage.setItem('inputsCurrencies', JSON.stringify(inputBlock));
  let inputItems = inputBlock.map((item, index) =>
      <InputItem key={item} title={item} index={index} deleteElem={deleteElem} />
  );

  if (error) {
    return <div>Ошибка: {error.message}</div>;
  } else if (!isLoaded) {
    return <div>Загрузка...</div>;
  } else {
  return (
    <Context.Provider value={{values, currenciesBlock, language: props.language, calculateRate, addElem}}>
      <CurrenciesBlock></CurrenciesBlock>
      <div className='inputs'>{inputItems}</div>
    </Context.Provider>
  )}
}